import * as React from 'react';
import { Drawer, List, StyleRulesCallback, Theme, withStyles } from '@material-ui/core';
import SideMenuItem from './SideMenuItem';
import { MENU } from '../../Constance/MenuConstance';

const styles: StyleRulesCallback = (theme: Theme) => ({
    drawerPaper: {
        display:"flex",
        left: 0,
        width: 200,
        overflow:"hidden"
    },
    toolbar: theme.mixins.toolbar,
    list: {
        marginTop: 100 + "px",
    },
})

interface IProps {
    classes: {
        drawerPaper: string;
        toolbar: string;
        list: string;
    }
    open: boolean;
}

class SideMenu extends React.Component<IProps> {
    constructor(props: IProps) {
        super(props);
    }
    public render() {
        return (
            <Drawer
                open={this.props.open}
                variant="permanent"
                className={this.props.classes.drawerPaper}
            >
                <List className={this.props.classes.list}>
                    {
                        MENU.map((menu, index) => {
                            return (
                                <SideMenuItem
                                    key={index}
                                    name={menu.name}
                                    subMenus={menu.subMenu}
                                />
                            )
                        })
                    }
                </List>
            </Drawer>

        );
    }
}

export default withStyles(styles)(SideMenu)



